/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import Page from 'src/layout/Page';
import SEO from 'components/seo';
import { Link, graphql } from 'gatsby';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { Magazine } from 'src/models';
import MagazineCard from 'src/components/MagazineCard';

export const pageQuery = graphql`
  query {
    allMdx(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            path
            title
          }
        }
      }
    }
  }
`

interface Props {
  data: {
    allMdx: {
      edges: { node: Magazine }[]
    }
  }
}
const Magazines = ({
  data: {
    allMdx: { edges },
  },
}: Props) => {
  console.log(edges);
  const magazineList = edges.filter(edge => !!edge.node.frontmatter.date);
  return (
    <Page pageName="Magazines" description="루비콘의 에피소드가 담긴 포스팅입니다">
      <Container>
        <Row>
          {
            magazineList.map(({ node }) => (
              <Col key={node.id} xs={12} sm={12} md={12} lg={12}>
                <MagazineCard magazine={node} />
              </Col>
            ))
          }
        </Row>
      </Container>
    </Page>
  );
};
export default Magazines;